import React from 'react';
import { Typography, makeStyles, createStyles, Avatar, List, ListItem, ListItemText, ListItemAvatar } from '@material-ui/core';
import classNames from 'classnames';

import { NodeDatum } from 'data/Simulation';
import { Recipe } from 'data/Recipes';

const useStyles = makeStyles( ( theme ) => createStyles( {
  root: {
    height: '100%',
    width: '100%',
    padding: theme.spacing( 2 ),
    display: 'flex',
    flexDirection: 'column',
    overflowY: 'auto',
  },
  header: {
    width: '100%',
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    '& > *:not( :last-child )': {
      marginRight: theme.spacing( 2 ),
    }
  },
  title: {
    flex: 1,
  },
  image: {
    width: 80,
    height: 80,
    border: '4px solid transparent',
  },
  recipeImage: {
    borderColor: 'green',
  },
  ingredientImage: {
    borderColor: 'orange',
  },
  list: {
  },
  listItem: {
    paddingLeft: theme.spacing( 1 ),
  },
  listItemAvatar: {
    pointerEvents: 'none',
  },
} ) );

interface Props
{
  recipes: Recipe[];
  node: NodeDatum | null;
  onNodeClick: ( nodeId: string ) => void;
}

const NodeInformation: React.FC<Props> = React.memo( ( { recipes, node, onNodeClick } ) =>
{
  const styles = useStyles();

  if( !node )
  {
    return null;
  }

  return (
    <div className={styles.root}>
      <div className={styles.header}>
        <Typography
          className={styles.title}
          variant="h6"
        >
          {node.data.name}
        </Typography>
        <Avatar
          className={classNames( styles.image, {
            [ styles.recipeImage ]: node.type === 'recipe',
            [ styles.ingredientImage ]: node.type === 'ingredient'
          } )}
          src={node.data.src}
          alt={node.data.name}
          title={node.data.name}
        />
      </div>
      {node.type === 'recipe' && (
        <>
          <Typography>Ingredients ({node.data.ingredients.length}):</Typography>
          <List
            dense={true}
            disablePadding={true}
            className={styles.list}
          >
            {node.data.ingredients.map( ( ingredient ) => (
              <ListItem
                key={ingredient.name}
                className={styles.listItem}
                button={true}
                onClick={() => onNodeClick( ingredient.id )}
              >
                <ListItemAvatar
                  className={styles.listItemAvatar}
                >
                  <Avatar
                    src={ingredient.src}
                    alt={ingredient.name}
                  />
                </ListItemAvatar>
                <ListItemText
                  primary={ingredient.name}
                />
              </ListItem>
            ) )}
          </List>
        </>
      )}
      {node.type === 'ingredient' && ( () =>
      {
        const usedInRecipes = recipes.filter( ( r ) => r.ingredients.some( ( i ) => i.name === node.data.name ) );

        return (
          <>
            <Typography>Used In ({usedInRecipes.length}):</Typography>
            <List
              dense={true}
              disablePadding={true}
              className={styles.list}
            >
              {usedInRecipes.map( ( recipe ) => (
                <ListItem
                  key={recipe.name}
                  className={styles.listItem}
                  button={true}
                  onClick={() => onNodeClick( recipe.id )}
                >
                  <ListItemAvatar
                    className={styles.listItemAvatar}
                  >
                    <Avatar
                      src={recipe.src}
                      alt={recipe.name}
                    />
                  </ListItemAvatar>
                  <ListItemText primary={recipe.name} />
                </ListItem>
              ) )}
            </List>
          </>
        );
      } )()}
    </div>
  );
} );

export default NodeInformation;
