import React, { useCallback, useState, useEffect } from 'react';
import { Tabs, Tab, Paper, makeStyles, createStyles } from '@material-ui/core';

import NodeInformation from 'components/NodeInformation';

import { NodeDatum } from 'data/Simulation';
import { Recipe, RecipeRestrictionMap } from 'data/Recipes';
import RecipeRestrictions from './RecipeRestrictions';

const useStyles = makeStyles( ( theme ) => createStyles( {
  root: {
    width: '100%',
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  content: {
    flex: 1,
    minHeight: 1,
    display: 'flex',
    flexDirection: 'column',
  }
} ) );

interface Props
{
  recipes: Recipe[];
  selectedNode: NodeDatum | null;
  onNodeClick: ( nodeId: string ) => void;
  restrictions: RecipeRestrictionMap;
  setRestrictions: ( restrictions: RecipeRestrictionMap ) => void;
}

const ControlPanel: React.FC<Props> = ( { recipes, selectedNode, onNodeClick, restrictions, setRestrictions } ) =>
{
  const styles = useStyles();

  const [ tab, setTab ] = useState( 0 );

  useEffect( () =>
  {
    if( selectedNode )
    {
      setTab( 0 );
    }
  }, [ selectedNode ] );

  const onTabChange = useCallback( ( _: React.ChangeEvent<{}>, newTab: number ) => setTab( newTab ), [] );

  return (
    <Paper
      square={true}
      className={styles.root}
    >
      <Tabs
        variant="fullWidth"
        centered={true}
        value={tab}
        onChange={onTabChange}
      >
        <Tab label="Selected Item" value={0} />
        <Tab label="Restrictions" value={1} />
      </Tabs>
      <div className={styles.content}>
        {tab === 0 && (
          <NodeInformation
            recipes={recipes}
            node={selectedNode}
            onNodeClick={onNodeClick}
          />
        )}
        {tab === 1 && (
          <RecipeRestrictions
            recipes={recipes}
            restrictions={restrictions}
            setRestrictions={setRestrictions}
          />
        )}
      </div>
    </Paper>
  );
};

export default ControlPanel;
