import React, { MutableRefObject, useRef, useEffect, useCallback, useState } from 'react';
import { makeStyles, createStyles } from '@material-ui/core';
import { Simulation } from 'd3-force';

import ScaleControl from 'components/ScaleControl';
import TranslationControl from 'components/TranslationControl';

import useForceUpdate from 'hooks/useForceUpdate';

import { NodeDatum, LinkDatum } from 'data/Simulation';

import createColorSet from 'utilities/colorSet';

const BORDER_THICKNESS = 4;

const useStyles = makeStyles( ( theme ) => createStyles( {
  container: {
    width: '100%',
    height: '100%',
    position: 'relative',
  },
  canvas: {
    width: '100%',
    height: '100%',
  },
  controls: {
    position: 'absolute',
    top: 0,
    right: 0,
    padding: theme.spacing( 1 ),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-end',
    '& > *:not( :last-child )': {
      marginBottom: theme.spacing( 1 ),
    },
  },
} ) );

const colorSet = createColorSet();

interface Props
{
  simulationRef: MutableRefObject<Simulation<NodeDatum, LinkDatum>>;
  nodesRef: MutableRefObject<NodeDatum[]>;
  linksRef: MutableRefObject<LinkDatum[]>;
  selectedNodeId: string | null;
  nodeRadius: number;
  onNodeDrag: ( node: NodeDatum, x: number, y: number ) => void;
  onNodeDragEnd: ( node: NodeDatum ) => void;
  onNodeClick: ( nodeId: string ) => void;
}

const CanvasGraph: React.FC<Props> = ( { simulationRef, nodesRef, linksRef, selectedNodeId, nodeRadius, onNodeDrag, onNodeDragEnd, onNodeClick } ) =>
{
  const styles = useStyles();

  const canvasRef = useRef<HTMLCanvasElement | null>( null );
  const imagesRef = useRef<{ [ src: string ]: HTMLImageElement | undefined }>( {} );

  const nextDrawRafRef = useRef<number | null>( null );

  const scaleRef = useRef( 0.5 );
  const translationRef = useRef( { x: 0, y: 0 } );

  const forceUpdate = useForceUpdate();

  const getImage = useCallback( ( src: string ) =>
  {
    let image = imagesRef.current[ src ];
    if( !image )
    {
      image = new Image();
      image.src = src;
      imagesRef.current[ src ] = image;
    }
    return image;
  }, [] );

  const onDrawCanvas = useCallback( () =>
  {
    nextDrawRafRef.current = null;

    if( !canvasRef.current )
    {
      console.error( 'No canvas found.' );
      return;
    }

    if( !canvasRef.current.getContext )
    {
      console.error( 'Canvas not supported.' );
      return;
    }

    const context = canvasRef.current.getContext( '2d' );
    if( !context )
    {
      console.error( '2D context not supported.' );
      return;
    }

    const width = canvasRef.current.width;
    const height = canvasRef.current.height;

    context.resetTransform();
    context.clearRect( 0, 0, width, height );

    context.translate( width / 2 + translationRef.current.x, height / 2 + translationRef.current.y );

    context.scale( scaleRef.current, scaleRef.current );

    context.lineWidth = 0.5;

    for( let link of linksRef.current )
    {
      context.strokeStyle = colorSet( link.target.data.id );
      context.beginPath();
      context.moveTo( link.source.x, link.source.y );
      context.lineTo( link.target.x, link.target.y );
      context.stroke();
    }

    context.lineWidth = BORDER_THICKNESS;

    const ellipseRadius = nodeRadius - BORDER_THICKNESS / 2;
    const imageSize = nodeRadius * 2 - BORDER_THICKNESS;

    for( let node of nodesRef.current )
    {
      context.strokeStyle = node.type === 'recipe' ? 'green' : 'orange';
      context.beginPath();
      context.ellipse( node.x, node.y, ellipseRadius, ellipseRadius, 2 * Math.PI, 0, 2 * Math.PI, false );

      const image = getImage( node.data.src );
      if( image.complete )
      {
        context.save();
        context.clip();
        context.drawImage( image, node.x - imageSize / 2, node.y - imageSize / 2, imageSize, imageSize );
        context.restore();
      }

      context.stroke();
    }

  }, [ nodeRadius, linksRef, nodesRef, getImage ] );

  const drawCanvas = useCallback( () =>
  {
    if( simulationRef.current.alpha() > simulationRef.current.alphaMin() )
    {
      return;
    }

    if( nextDrawRafRef.current !== null )
    {
      return;
    }

    nextDrawRafRef.current = window.requestAnimationFrame( onDrawCanvas );

  }, [ onDrawCanvas, simulationRef ] );

  const setScaleBounded = useCallback( ( newScale: number ) =>
  {
    scaleRef.current = Math.max( 0.1, Math.round( ( newScale ) * 100 ) / 100 );

    forceUpdate();

    drawCanvas();
  }, [ drawCanvas, forceUpdate ] );

  const onResetTranslation = useCallback( () =>
  {
    translationRef.current = { x: 0, y: 0 };

    forceUpdate();

    drawCanvas();
  }, [ drawCanvas, forceUpdate ] );

  const onResize = useCallback( () =>
  {
    if( canvasRef.current )
    {
      canvasRef.current.width = canvasRef.current.clientWidth;
      canvasRef.current.height = canvasRef.current.clientHeight;

      drawCanvas();
    }
  }, [ drawCanvas ] );

  useEffect( () =>
  {
    window.addEventListener( 'resize', onResize );

    return () => window.removeEventListener( 'resize', onResize );
  }, [ onResize ] );

  const getCanvasRef = useCallback( ( canvas: HTMLCanvasElement | null ) =>
  {
    canvasRef.current = canvas;

    onResize();

  }, [ onResize ] );

  useEffect( () =>
  {
    const sim = simulationRef.current;

    function onTick()
    {
      onDrawCanvas();
    }

    sim.on( `tick.${CanvasGraph.name}`, onTick );

    return () =>
    {
      sim.on( `tick.${CanvasGraph.name}`, null );
    };
  }, [ simulationRef, onDrawCanvas, forceUpdate ] );

  const draggingBackgroundRef = useRef( false );
  const draggingNodeRef = useRef<NodeDatum | null>( null );
  const firstMoveRef = useRef( true );
  const movedRef = useRef( false );

  const [ mouseCoords, setMouseCoords ] = useState( { clientX: 0, clientY: 0 } );

  const getSimulationCoords = useCallback( ( e: { clientX: number, clientY: number } ) =>
  {
    if( !canvasRef.current )
    {
      return { x: 0, y: 0 };
    }

    const { left, top, width, height } = canvasRef.current.getBoundingClientRect();

    const x = ( e.clientX - left - width / 2 - translationRef.current.x ) / scaleRef.current;
    const y = ( e.clientY - top - height / 2 - translationRef.current.y ) / scaleRef.current;
    return { x, y };
  }, [] );

  const getNodeFromMouseCoords = useCallback( ( e: { clientX: number, clientY: number } ) =>
  {
    const { x, y } = getSimulationCoords( e );

    return nodesRef.current.find( ( n ) =>
    {
      const xDistance = x - n.x;
      const yDistance = y - n.y;

      const distanceSq = xDistance * xDistance + yDistance * yDistance;

      return distanceSq <= nodeRadius * nodeRadius;
    } );
  }, [ getSimulationCoords, nodeRadius, nodesRef ] );

  const onMouseDown = ( e: React.MouseEvent<HTMLElement> ) =>
  {
    e.preventDefault();
    e.stopPropagation();

    const node = getNodeFromMouseCoords( e );

    if( node )
    {
      draggingNodeRef.current = node;
      draggingBackgroundRef.current = false;
    }
    else
    {
      draggingNodeRef.current = null;
      draggingBackgroundRef.current = true;
    }

    firstMoveRef.current = true;
    movedRef.current = false;
  };

  useEffect( () =>
  {
    const onMouseMove = ( e: MouseEvent ) =>
    {
      setMouseCoords( {
        clientX: e.clientX,
        clientY: e.clientY,
      } );

      if( draggingBackgroundRef.current )
      {
        const movementX = e.movementX;
        const movementY = e.movementY;

        translationRef.current = {
          x: translationRef.current.x + movementX,
          y: translationRef.current.y + movementY,
        };

        forceUpdate();

        if( simulationRef.current.alpha() < simulationRef.current.alphaMin() )
        {
          drawCanvas();
        }
      }
      else
      {

        if( !canvasRef.current )
        {
          console.error( 'No container ref on mousemove.' );
          return;
        }

        if( !draggingNodeRef.current )
        {
          return;
        }

        const { x, y } = getSimulationCoords( e );

        if( firstMoveRef.current )
        {
          firstMoveRef.current = false;
          return;
        }

        movedRef.current = true;
        onNodeDrag( draggingNodeRef.current, x, y );
      }
    };

    window.addEventListener( 'mousemove', onMouseMove );

    return () => window.removeEventListener( 'mousemove', onMouseMove );

  }, [ drawCanvas, getSimulationCoords, onNodeDrag, simulationRef, forceUpdate ] );

  useEffect( () =>
  {
    const onMouseUp = ( e: MouseEvent ) =>
    {
      if( draggingNodeRef.current
        && movedRef.current )
      {
        onNodeDragEnd( draggingNodeRef.current );
      }

      draggingNodeRef.current = null;
      draggingBackgroundRef.current = false;
      firstMoveRef.current = true;
    };

    window.addEventListener( 'mouseup', onMouseUp );

    return () => window.removeEventListener( 'mouseup', onMouseUp );

  }, [ onNodeDragEnd ] );

  const onMouseClick = useCallback( ( e: React.MouseEvent<HTMLElement> ) =>
  {
    if( movedRef.current )
    {
      return;
    }

    const node = getNodeFromMouseCoords( e );
    if( !node )
    {
      return;
    }

    onNodeClick( node.data.id );

  }, [ onNodeClick, getNodeFromMouseCoords ] );

  const hoveringNode = getNodeFromMouseCoords( mouseCoords );

  return (
    <div
      className={styles.container}
    >
      <canvas
        ref={getCanvasRef}
        className={styles.canvas}
        title={hoveringNode ? hoveringNode.data.name : undefined}
        onMouseDown={onMouseDown}
        onClick={onMouseClick}
      />
      <div className={styles.controls}>
        <ScaleControl
          scale={scaleRef.current}
          setScale={setScaleBounded}
        />
        <TranslationControl
          centered={translationRef.current.x === 0 && translationRef.current.y === 0}
          onResetTranslation={onResetTranslation}
        />
      </div>
    </div>
  );
};

export default CanvasGraph;
