import React, { useRef, useEffect, useState, useCallback } from 'react';
import { makeStyles, createStyles, Grid } from '@material-ui/core';
import { forceSimulation, forceLink, forceManyBody, forceCenter, forceRadial, forceCollide, Simulation } from 'd3-force';

import ControlPanel from 'components/ControlPanel';
import HtmlGraph from 'components/HtmlGraph';
import CanvasGraph from 'components/CanvasGraph';

import useRefInit from 'hooks/useRefInit';

import { Recipe, Ingredient, RecipeRestrictionMap, RecipeRestriction } from 'data/Recipes';
import { NodeDatum, LinkDatum, UninitializedNodeDatum, UninitializedLinkDatum } from 'data/Simulation';

import { distinct } from 'utilities';

const NODE_RADIUS = 16;

const useStyles = makeStyles( ( theme ) => createStyles( {
  container: {
    height: '100%',
    overflow: 'hidden',
  },
  controlPanel: {
    zIndex: 1,
    padding: theme.spacing( 1 ),
    height: '100%',
  },
  graph: {
    height: '100%',
    position: 'relative',
  },
} ) );

const formatLinkId = ( recipe: Recipe, ingredient: Ingredient ) => `${recipe.id}->${ingredient.id}`;

const createIngredientNode = ( ingredient: Ingredient ): UninitializedNodeDatum => ( {
  type: 'ingredient',
  data: ingredient,
} );

const createRecipeNode = ( recipe: Recipe ): UninitializedNodeDatum => ( {
  type: 'recipe',
  data: recipe,
} );

function isRecipeRestricted( restrictions: RecipeRestrictionMap, recipe: Recipe )
{
  return recipe.ingredients.some( ( ingredient ) =>
  {
    if( restrictions[ RecipeRestriction.Vegan ]
      && ingredient.certification !== 'vegan' )
    {
      return true;
    }

    if( restrictions[ RecipeRestriction.Vegetarian ]
      && !( ingredient.certification === 'vegan'
        || ingredient.certification === 'vegetarian' ) )
    {
      return true;
    }

    if( restrictions[ RecipeRestriction.NoDairy ]
      && ( ingredient.contains.includes( 'Milk' )
        || ingredient.contains.includes( 'a milk derivative **not a source of lactose' ) ) )
    {
      return true;
    }

    if( restrictions[ RecipeRestriction.NoEgg ]
      && ingredient.contains.includes( 'Egg' ) )
    {
      return true;
    }

    if( restrictions[ RecipeRestriction.NoSoy ]
      && ingredient.contains.includes( 'Soy' ) )
    {
      return true;
    }

    return false;
  } );
}

interface Props
{
  recipes: Recipe[];
}

const RecipeGraph: React.FC<Props> = ( { recipes } ) =>
{
  const styles = useStyles();

  const [ selectedNode, setSelectedNode ] = useState<NodeDatum | null>( null );
  const [ restrictions, setRestrictions ] = useState<RecipeRestrictionMap>( {
    [ RecipeRestriction.Vegetarian ]: false,
    [ RecipeRestriction.Vegan ]: false,
    [ RecipeRestriction.NoDairy ]: false,
    [ RecipeRestriction.NoEgg ]: false,
    [ RecipeRestriction.NoSoy ]: false,
  } );

  const initializedRef = useRef( false );

  const simulationRef = useRefInit<Simulation<NodeDatum, LinkDatum>>( () =>
  {
    return forceSimulation<NodeDatum, LinkDatum>()
      .force( 'charge', forceManyBody().strength( -NODE_RADIUS * 20 ) )
      .force( 'center', forceCenter( 0, 0 ) )
      .force( 'radial', forceRadial( 500 ) )
      .force( 'collide', forceCollide( NODE_RADIUS ) )
      .on( `end.${RecipeGraph.name}`, () => console.log( 'SIMULATION END' ) );
  } );

  const nodesRef = useRef<NodeDatum[]>( [] );
  useEffect( () =>
  {
    const restrictedRecipes = recipes.filter( ( recipe ) => !isRecipeRestricted( restrictions, recipe ) );
    const restrictedIngredients = distinct( restrictedRecipes.flatMap( ( recipe ) => recipe.ingredients ), ( ingredient ) => ingredient.name );

    const existingNodes = new Set( nodesRef.current.map( ( n ) => n.data.id ) );
    const allNodes = new Set( [
      ...restrictedIngredients.map( ( ingredient ) => ingredient.id ),
      ...restrictedRecipes.map( ( recipe ) => recipe.id ),
    ] );

    nodesRef.current = [
      ...nodesRef.current.filter( ( n ) => allNodes.has( n.data.id ) ),
      ...restrictedIngredients.filter( ( ingredient ) => !existingNodes.has( ingredient.id ) ).map( createIngredientNode ),
      ...restrictedRecipes.filter( ( recipe ) => !existingNodes.has( recipe.id ) ).map( createRecipeNode ),
    ] as NodeDatum[];

    simulationRef.current.nodes( nodesRef.current );
    simulationRef.current.alpha( 1 );
    simulationRef.current.restart();

    if( !initializedRef.current )
    {
      initializedRef.current = true;

      setSelectedNode( nodesRef.current.find( ( node ) => node.data.name === 'Beefy 5-Layer Burrito' ) || nodesRef.current[ 0 ] || null );
    }

  }, [ simulationRef, nodesRef, recipes, restrictions ] );

  useEffect( () =>
  {
    if( selectedNode )
    {
      if( selectedNode.type === 'recipe' )
      {
        if( isRecipeRestricted( restrictions, selectedNode.data ) )
        {
          setSelectedNode( null );
        }
      }
      else if( selectedNode.type === 'ingredient' )
      {
        const restrictedIngredients = recipes
          .filter( ( recipe ) => !isRecipeRestricted( restrictions, recipe ) )
          .flatMap( ( recipe ) => recipe.ingredients );

        if( !restrictedIngredients.find( ( ingredient ) => ingredient.id === selectedNode.data.id ) )
        {
          setSelectedNode( null );
        }
      }
    }
  }, [ recipes, restrictions, selectedNode ] );

  const linksRef = useRef<LinkDatum[]>( [] );
  useEffect( () =>
  {
    linksRef.current = recipes
      .filter( ( recipe ) => !isRecipeRestricted( restrictions, recipe ) )
      .flatMap<UninitializedLinkDatum>( ( recipe ) =>
        recipe.ingredients
          .map( ( ingredient ) => ( {
            id: formatLinkId( recipe, ingredient ),
            source: recipe.id,
            target: ingredient.id,
          } ) )
      ) as LinkDatum[];

    simulationRef.current
      .force( 'link',
        forceLink<NodeDatum, LinkDatum>( linksRef.current ).id( ( node ) => node.data.id )
          .distance( NODE_RADIUS * 10 )
      );

  }, [ simulationRef, recipes, restrictions ] );

  const onNodeDrag = useCallback( ( node: NodeDatum, x: number, y: number ) =>
  {
    node.fx = x;
    node.fy = y;
    simulationRef.current.alpha( 1.0 );
    simulationRef.current.restart();
  }, [ simulationRef ] );

  const onNodeDragEnd = useCallback( ( node: NodeDatum ) =>
  {
    node.fx = null;
    node.fy = null;
    simulationRef.current.alpha( 1.0 );
    simulationRef.current.restart();
  }, [ simulationRef ] );

  const onNodeClick = useCallback( ( nodeId: string ) =>
  {
    const node = nodesRef.current.find( ( n ) => n.data.id === nodeId );
    if( node )
    {
      setSelectedNode( node );
    }
  }, [] );

  const GraphComponent = window.location.hash === '#html' ? HtmlGraph : CanvasGraph;

  return (
    <Grid
      className={styles.container}
      container={true}
      spacing={0}
    >
      <Grid
        className={styles.controlPanel}
        item={true}
        xs={4}
        md={3}
        xl={2}
      >
        <ControlPanel
          recipes={recipes}
          selectedNode={selectedNode}
          onNodeClick={onNodeClick}
          restrictions={restrictions}
          setRestrictions={setRestrictions}
        />
      </Grid>
      <Grid
        className={styles.graph}
        item={true}
        xs={true}
      >
        <GraphComponent
          simulationRef={simulationRef}
          nodesRef={nodesRef}
          linksRef={linksRef}
          selectedNodeId={selectedNode ? selectedNode.data.id : null}
          nodeRadius={NODE_RADIUS}
          onNodeDrag={onNodeDrag}
          onNodeDragEnd={onNodeDragEnd}
          onNodeClick={onNodeClick}
        />
      </Grid>
    </Grid>
  );
};

export default RecipeGraph;
