export { default as recipes } from 'data/RecipesData';

export type IngredientContains = 'Wheat' | 'Egg' | 'Milk' | 'Soy' | 'a milk derivative **not a source of lactose';
export type IngredientCertification = 'vegan' | 'vegetarian';

export interface Ingredient
{
  name: string;
  subingredients: string;
  contains: IngredientContains[];
  certification: IngredientCertification | null;
  id: string;
  src: string;
}

export interface Recipe
{
  name: string;
  category: string;
  ingredients: Ingredient[];
  id: string;
  src: string;
}

export enum RecipeRestriction
{
  Vegetarian = 'vegetarian',
  Vegan = 'vegan',
  NoDairy = 'no-dairy',
  NoEgg = 'no-egg',
  NoSoy = 'no-soy',
}

export type RecipeRestrictionMap = { [ restriction in RecipeRestriction ]: boolean };
