import React, { useEffect, useRef, useCallback, useState, MutableRefObject } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core';
import classNames from 'classnames';
import { Simulation } from 'd3-force';

import ScaleControl from 'components/ScaleControl';
import TranslationControl from 'components/TranslationControl';

import { NodeDatum, LinkDatum } from 'data/Simulation';

import createColorSet from 'utilities/colorSet';

interface StyleProps
{
  nodeRadius: number;
}

const useStyles = makeStyles<Theme, StyleProps>( ( theme ) => createStyles( {
  root: {
    position: 'relative',
    width: '100%',
    height: '100%',
    overflow: 'visible',
    userSelect: 'none',
  },
  graph: {
    position: 'relative',
    left: '50%',
    top: '50%',
    width: 0,
    height: 0,
  },
  link: {
    position: 'absolute',
    left: 0,
    top: 0,
    height: 1,
    transformOrigin: 'top left',
  },
  node: ( { nodeRadius } ) => ( {
    position: 'absolute',
    left: 0,
    top: 0,
    width: nodeRadius * 2,
    height: nodeRadius * 2,
    borderRadius: '50%',
    borderWidth: 4,
    borderStyle: 'solid',
  } ),
  recipeNode: {
    borderColor: 'green',
  },
  ingredientNode: {
    borderColor: 'orange',
  },
  selectedNode: {
    '&:after': {
      content: '""',
      position: 'absolute',
      display: 'inline-block',
      zIndex: -1,
      left: -4,
      right: -4,
      top: -4,
      bottom: -4,
      borderRadius: '50%',
      boxShadow: '0px 0px 10px 3px #FFFFFF',
      animation: '$selection-pulse 1s ease-out infinite alternate',
    },
  },
  '@keyframes selection-pulse': {
    '0%': {
      opacity: 0,
    },
    '100%': {
      opacity: 1,
    },
  },
  img: {
    width: '100%',
    height: '100%',
    borderRadius: '50%',
    pointerEvents: 'none',
  },
  controls: {
    position: 'absolute',
    top: 0,
    right: 0,
    padding: theme.spacing( 1 ),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-end',
    '& > *:not( :last-child )': {
      marginBottom: theme.spacing( 1 ),
    },
  },
} ) );

interface Props
{
  simulationRef: MutableRefObject<Simulation<NodeDatum, LinkDatum>>;
  nodesRef: MutableRefObject<NodeDatum[]>;
  linksRef: MutableRefObject<LinkDatum[]>;
  selectedNodeId: string | null;
  nodeRadius: number;
  onNodeDrag: ( node: NodeDatum, x: number, y: number ) => void;
  onNodeDragEnd: ( node: NodeDatum ) => void;
  onNodeClick: ( nodeId: string ) => void;
}

const colors = createColorSet();

const HtmlGraph: React.FC<Props> = ( { simulationRef, nodesRef, linksRef, selectedNodeId, nodeRadius, onNodeDrag, onNodeDragEnd, onNodeClick } ) =>
{
  const styles = useStyles( { nodeRadius } );

  const [ , setUpdateCount ] = useState( 0 );
  useEffect( () =>
  {
    const sim = simulationRef.current;

    function onTick()
    {
      setUpdateCount( ( oldUpdateCount ) => oldUpdateCount + 1 );
    }

    sim.on( `tick.${HtmlGraph.name}`, onTick );

    return () =>
    {
      sim.on( `tick.${HtmlGraph.name}`, null );
    };
  }, [ simulationRef ] );

  const containerRef = useRef<HTMLDivElement | null>( null );
  const draggingBackgroundRef = useRef( false );
  const draggingNodeRef = useRef<NodeDatum | null>( null );
  const firstMoveRef = useRef( true );
  const movedRef = useRef( false );

  const [ scale, setScale ] = useState( 0.5 );
  const [ translation, setTranslation ] = useState( { x: 0, y: 0 } );
  const setScaleBounded = useCallback( ( newScale: number ) => setScale( Math.max( 0.1, Math.round( ( newScale ) * 100 ) / 100 ) ), [] );

  const onResetTranslation = useCallback( () =>
  {
    setTranslation( { x: 0, y: 0 } );
  }, [] );

  const onBackgroundMouseDown = useCallback( () =>
  {
    draggingNodeRef.current = null;
    draggingBackgroundRef.current = true;
    firstMoveRef.current = true;
    movedRef.current = false;
  }, [] );

  const onNodeMouseDown = ( node: NodeDatum, e: React.MouseEvent<HTMLElement> ) =>
  {
    e.preventDefault();
    e.stopPropagation();

    draggingNodeRef.current = node;
    draggingBackgroundRef.current = false;
    firstMoveRef.current = true;
    movedRef.current = false;
  };

  useEffect( () =>
  {
    const onMouseMove = ( e: MouseEvent ) =>
    {
      if( draggingBackgroundRef.current )
      {
        const movementX = e.movementX;
        const movementY = e.movementY;

        setTranslation( ( { x, y } ) => ( {
          x: x + movementX,
          y: y + movementY,
        } ) );
      }
      else
      {

        if( !containerRef.current )
        {
          console.error( 'No container ref on mousemove.' );
          return;
        }

        if( !draggingNodeRef.current )
        {
          return;
        }

        const { left, top, width, height } = containerRef.current.getBoundingClientRect();

        const x = ( e.clientX - left - width / 2 - translation.x ) / scale;
        const y = ( e.clientY - top - height / 2 - translation.y ) / scale;

        if( firstMoveRef.current )
        {
          firstMoveRef.current = false;
          return;
        }

        movedRef.current = true;
        onNodeDrag( draggingNodeRef.current, x, y );
      }
    };

    window.addEventListener( 'mousemove', onMouseMove );

    return () => window.removeEventListener( 'mousemove', onMouseMove );

  }, [ onNodeDrag, scale, translation ] );

  useEffect( () =>
  {
    const onMouseUp = ( e: MouseEvent ) =>
    {
      if( draggingNodeRef.current
        && movedRef.current )
      {
        onNodeDragEnd( draggingNodeRef.current );
      }

      draggingNodeRef.current = null;
      draggingBackgroundRef.current = false;
      firstMoveRef.current = true;
    };

    window.addEventListener( 'mouseup', onMouseUp );

    return () => window.removeEventListener( 'mouseup', onMouseUp );

  }, [ onNodeDragEnd ] );

  const onNodeMouseClick = useCallback( ( node: NodeDatum ) =>
  {
    if( !movedRef.current )
    {
      onNodeClick( node.data.id );
    }
  }, [ onNodeClick ] );

  return (
    <div
      ref={containerRef}
      className={styles.root}
      onMouseDown={onBackgroundMouseDown}
    >
      <div
        className={styles.graph}
        style={{
          transform: `translate( ${translation.x}px, ${translation.y}px ) scale( ${scale} )`,
        }}
      >
        {linksRef.current.map( ( link ) =>
        {
          const xDistance = link.target.x - link.source.x;
          const yDistance = link.target.y - link.source.y;

          const distance = Math.sqrt( xDistance * xDistance + yDistance * yDistance );

          const rotation = Math.atan2( yDistance, xDistance );

          return (
            <div
              key={link.id}
              className={styles.link}
              style={{
                width: distance,
                transform: `translate( ${link.source.x}px, ${link.source.y}px ) rotate( ${rotation}rad )`,
                backgroundColor: colors( link.target.data.id, 100, 50 ),
              }}
            />
          );
        } )}
        {nodesRef.current.map( ( node ) => (
          <div
            key={node.data.id}
            title={node.data.name}
            className={classNames( styles.node, {
              [ styles.recipeNode ]: node.type === 'recipe',
              [ styles.ingredientNode ]: node.type === 'ingredient',
              [ styles.selectedNode ]: node.data.id === selectedNodeId,
            } )}
            style={{
              transform: `translate( ${node.x - nodeRadius}px, ${node.y - nodeRadius}px )`,
            }}
            onMouseDown={( e ) => onNodeMouseDown( node, e )}
            onClick={() => onNodeMouseClick( node )}
          >
            {node.data.src && (
              <img
                src={node.data.src}
                alt={node.data.name}
                className={styles.img}
              />
            )}
          </div>
        ) )}
      </div>
      <div className={styles.controls}>
        <ScaleControl
          scale={scale}
          setScale={setScaleBounded}
        />
        <TranslationControl
          centered={translation.x === 0 && translation.y === 0}
          onResetTranslation={onResetTranslation}
        />
      </div>
    </div>
  );
};

export default HtmlGraph;
