import React from 'react';
import { Switch, FormControlLabel, makeStyles, createStyles } from '@material-ui/core';

import { Recipe, RecipeRestrictionMap, RecipeRestriction } from 'data/Recipes';

const useStyles = makeStyles( ( theme ) => createStyles( {
  root: {
    width: '100%',
    height: '100%',
    overflowY: 'auto',
    display: 'flex',
    flexDirection: 'column',
    padding: theme.spacing( 2 ),
  },
  restrictionLabelRoot: {
    marginLeft: 0,
  },
  restrictionLabelLabel: {
    flex: 1,
  },
} ) );

interface Props
{
  recipes: Recipe[];
  restrictions: RecipeRestrictionMap;
  setRestrictions: ( restrictions: RecipeRestrictionMap ) => void;
}

const RESTRICTIONS = [
  { name: 'Vegetarian', value: RecipeRestriction.Vegetarian },
  { name: 'Vegan', value: RecipeRestriction.Vegan },
  { name: 'No Dairy', value: RecipeRestriction.NoDairy },
  { name: 'No Eggs', value: RecipeRestriction.NoEgg },
  { name: 'No Soy', value: RecipeRestriction.NoSoy },
];

const RecipeRestrictions: React.FC<Props> = ( { recipes, restrictions, setRestrictions } ) =>
{
  const styles = useStyles();

  return (
    <div className={styles.root}>
      {RESTRICTIONS.map( ( { name, value } ) => (
        <FormControlLabel
          key={value}
          label={name}
          labelPlacement="start"
          classes={{
            root: styles.restrictionLabelRoot,
            label: styles.restrictionLabelLabel,
          }}
          control={(
            <Switch
              checked={!!restrictions[ value ]}
              onChange={( _, checked ) => setRestrictions( {
                ...restrictions,
                [ value ]: checked,
              } )}
            />
          )}
        />
      ) )}
    </div>
  );
};

export default RecipeRestrictions;
